<template>
	<div id="order-op">
		<public-header :title="pageInfo[type]['page_title']"></public-header>
		<div class="content">
			<template v-if="type == 'direct_deliveryer'">
				<div class="padding-15-lr padding-10-tb font-14 c-gray">选择配送员</div>
				<van-radio-group v-model="itemSelectedKey">
					<van-cell-group>
						<van-cell :title="item.title" clickable @click="onSelectItem(index)" class="no-fix" v-for="(item, index) in resultData" :key="index">
							<van-radio :name="index" />
						</van-cell>
					</van-cell-group>
				</van-radio-group>
			</template>
			<template v-else>
				<div class="padding-15-lr padding-10-tb font-14 c-gray">取消订单理由</div>
				<van-radio-group v-model="itemSelectedKey">
					<van-cell-group>
						<van-cell :title="item" clickable @click="onSelectItem(index)" class="no-fix" v-for="(item, index) in resultData" :key="index">
							<van-radio :name="index" />
						</van-cell>
						<van-field
							:placeholder="type == 'cancel'  ? '填写备注（选填）' : '输入回复'"
							readonly
						/>
						<van-field
							v-model="remark"
							type="textarea"
							:placeholder="type == 'cancel'  ? '取消订单备注' : '输入回复'"
							rows="5"
						/>
					</van-cell-group>
				</van-radio-group>
			</template>
			<div class="padding-15">
				<van-button size="normal" block class="bg-info font-16" @click='onSubmit'>提交</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader
		},
		data() {
			return {
				showPreLoading: true,
				resultData: {},
				itemSelectedKey: 0,
				remark: '',
				pageInfo: {
					cancel: {
						page_title: '取消订单理由',
						requestUrl: 'manage/order/takeout/cancel'
					},
					direct_deliveryer: {
						page_title: '指定配送员',
						requestUrl: 'manage/order/takeout/deliveryer'
					},
					reply: {
						page_title: '催单回复',
						requestUrl: 'manage/order/takeout/reply'
					}

				}
			}
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'manage/order/takeout/op',
					data: {
						type: that.type
					},
					success: function(res) {
						that.resultData = res.data;
					}
				});
			},
			onSelectItem(index) {
				this.itemSelectedKey = index;
				if(this.type == 'reply') {
					this.remark = this.remark + this.resultData[index];
				}
			},
			onSubmit() {
				var params = {
					url: this.pageInfo[this.type].requestUrl,
					confirm: this.tip,
					successUrl: this.util.getUrl({path: 'pages/order/index', query: {status: this.order_status}})
				};
				if(this.type == 'cancel') {
					params['data'] = {
						id: this.order_id,
						reason: this.itemSelectedKey,
						remark: this.remark,
					};
					var that = this;
					setData({
						vue: that,
						url: params.url,
						data: params.data,
						confirm: params.confirm,
						success: function(result) {
							that.util.$toast(result, params.successUrl, 1000, 'replace');
						}
					});
					return false;
				} else if(this.type == 'direct_deliveryer') {
					params['data'] = {
						id: this.order_id,
						deliveryer_id: this.itemSelectedKey
					}
				} else if(this.type == 'reply') {
					params['data'] = {
						id: this.order_id,
						reply: this.remark
					}
				} 
				this.util.jspost(params);
			}
		},
		mounted() {
			this.onLoad();
		},
		created(){
			this.query = this.$route.query;
			if(this.query) {
				this.order_id = this.query.order_id;
				this.tip = this.query.tip;
				this.order_status = this.query.order_status;
				this.type = this.query.type;
				if(this.type == 'cancel_refund') {
					this.type = cancel;
				}
			}
		}
	}
</script>

<style>
	#order-op .van-field{
		padding-top: 0px;
	}
	#order-op .van-field textarea{
		background-color: #f5f5f5;
		padding: 10px;
	}
</style>


